.buy {
  margin-bottom: 110px;
  text-align: center;
  position: relative;
  overflow: hidden;

  &__img {
    margin-bottom: 30px;
    margin: 0 auto;
  }

  & .buy__btn {
    margin: 0 auto;
    margin-bottom: 10px;
  }

  &__bg {
    background: linear-gradient(
      180deg,
      #bc10d8 0%,
      rgba(80, 0, 250, 0.93) 100%
    );
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    font-weight: 700;
    font-size: 400px;
    line-height: 400px;
    opacity: 0.1;
    position: absolute;
    z-index: -1;
    animation: line-text 6s linear 1s infinite;
  }
}

@keyframes line-text {
  from {
    transform: translateX(115%);
    transform-origin: left center;
  }
  to {
    transform: translateX(-115%);
    transform-origin: right center;
  }
}

@media (max-width: 568px) {
  .buy__bg {
    font-size: 200px;
  }
}
